import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "./AppointmentForm.css";

const AppointmentForm = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    patientname: "",
    age: "",
    gender: "",
    specialist: "",
    doctor: "",
    appointment_date: "",
    appointment_time: "",
  });

  function handleSubmit(e) {
    e.preventDefault();

    fetch(
      "https://potential-yodel-v6p765vp5rv6hxqwq-3168.app.github.dev/appointments",
      {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      }
    )
      .then((res) => {
        if (res.ok) {
          return res.json();
        }
        throw new Error("Network response was not ok.");
      })
      .then((data) => {
        console.log(formData);
        toast.success("Booking Appoinment Successfully");
        setFormData({
          patientname: "",
          age: "",
          gender: "",
          specialist: "",
          doctor: "",
          appointment_date: "",
          appointment_time: "",
        });
      })
      .catch((error) => {
        console.error("Error:", error);
        toast.error("Registration failed. Please try again.");
      });
  }

  function handleChange(e) {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  }

  return (
    <div className="register-container">
      <h2 className="title" s>
        Book Appointment
      </h2>
      <form onSubmit={handleSubmit}>
        <div className="form-row">
          <div style={{ paddingBottom: "10px" }}>
            <input
              type="text"
              id="patientname"
              placeholder="Enter Patient Name"
              name="patientname"
              value={formData.patientname}
              onChange={handleChange}
              className="form-control input-field"
              required
            />
          </div>
          <div style={{ paddingBottom: "10px" }}>
            <input
              type="number"
              id="age"
              placeholder="Enter Age"
              name="age"
              value={formData.age}
              onChange={handleChange}
              className="form-control input-field"
              required
            />
          </div>
          <div style={{ paddingBottom: "10px" }} className="radio-group">
            <label>Gender:</label>
            {"      "}
            <input
              type="radio"
              id="male"
              name="gender"
              value="male"
              onChange={handleChange}
              required
            />{" "}
            {"      "}
            <label htmlFor="male">Male</label>
            <input
              type="radio"
              id="female"
              name="gender"
              value="female"
              onChange={handleChange}
              required
            />
            <label htmlFor="female">Female</label>
            <input
              type="radio"
              id="other"
              name="gender"
              value="other"
              onChange={handleChange}
              required
            />
            <label htmlFor="other">Other</label>
          </div>
          <div style={{ paddingBottom: "10px" }}>
            <select
              id="specialist"
              name="specialist"
              value={formData.specialist}
              onChange={handleChange}
              className="form-control input-field"
              required
            >
              <option value="">Select Specialist</option>
              <option value="Cardiologist">Cardiologist</option>
              <option value="Dermatologist">Dermatologist</option>
              <option value="Orthopedist">Orthopedist</option>
            </select>
          </div>
          <div style={{ paddingBottom: "10px" }}>
            <select
              id="doctor"
              name="doctor"
              value={formData.doctor}
              onChange={handleChange}
              className="form-control input-field"
              required
            >
              <option value="">Select Doctor</option>
              {formData.specialist === "Cardiologist" && (
                <>
                  <option value="Dr. Srividya">Dr. Srividya</option>
                  <option value="Dr. Sri">Dr. Sri</option>
                </>
              )}
              {formData.specialist === "Dermatologist" && (
                <>
                  <option value="Dr. Sowjanya">Dr. Sowjanya</option>
                  <option value="Dr. Laxman Reddy">Dr. Laxman Reddy</option>
                </>
              )}
              {formData.specialist === "Orthopedist" && (
                <>
                  <option value="Dr. Vamshi reddy">Dr. Vamshi reddy</option>
                  <option value="Dr. Nandhitha Reddy">
                    Dr. Nandhitha Reddy
                  </option>
                </>
              )}
            </select>
          </div>
          <div style={{ paddingBottom: "10px" }}>
            <input
              type="date"
              id="appointment_date"
              name="appointment_date"
              value={formData.appointment_date}
              onChange={handleChange}
              className="form-control input-field"
              required
            />
          </div>
          <div style={{ paddingBottom: "10px" }}>
            <input
              type="time"
              id="appointment_time"
              name="appointment_time"
              value={formData.appointment_time}
              onChange={handleChange}
              className="form-control input-field"
              required
            />
          </div>
          <button type="submit" className="btn btn-success">
            Book Appointment
          </button>
        </div>
      </form>
      <ToastContainer />
    </div>
  );
};

export default AppointmentForm;
